"use client";
import { useToursQuery } from "@/hooks/queries/useToursQuery";
import React from "react";
import Container from "./Container";
import Title from "./ui/Title";

const ToursList = () => {
  const { data, isLoading, isError } = useToursQuery();

  return (
    <Container>
      <section>COUNTODWN TO MOST RECENT </section>

      <section className="flex flex-col">
        {data &&
          data.map((tourData) => (
            <div className="flex">
              <figure className="w-1/2">
                <img className="h-auto w-full" src={`http://localhost:4444/images/tours/${tourData.image1}`} />
              </figure>
              <section className="w-1/2 border-neutral-200 border-[2px] relative flex items-center">
                <Title text={tourData.title} />
              </section>
            </div>
          ))}
      </section>
    </Container>
  );
};

export default ToursList;
